
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
.fa-icon {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}
body,
html,
#db {
  margin: 0;
  padding: 0;
  height: 100vh;
}
#db {
  font-family: Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
