}
label {
  margin: 0 0 1rem;
}
.grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
}
.grid__col--3 {
  -webkit-box-ordinal-group: 2;
      -ms-flex-order: 1;
          order: 1;
  width: 100%;
  padding: 0 1rem;
}
.grid__col--9 {
  -webkit-box-ordinal-group: 3;
      -ms-flex-order: 2;
          order: 2;
  width: 100%;
}
@media (min-width: 625px) {
.grid {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
}
[class*="grid__col--"] {
    height: 100%;
}
.grid__col--3 {
    -webkit-box-ordinal-group: 3;
        -ms-flex-order: 2;
            order: 2;
    width: 25%;
    border-left: 1px solid rgba(0, 0, 0, 0.125);
}
.grid__col--9 {
    -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
            order: 1;
    width: 75%;
}
[class*="grid__col--"]:last-child,
  [class*="grid__col--"]:only-child {
    margin-right: 0;
}
}



